/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

html {
  scroll-behavior: smooth; }

body {
  background: #0b111e; }

/* header section */
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  background: #000;
  padding: 0 7%;
  z-index: 1000; }
  .header .logo {
    padding: 16px 0;
    margin-right: auto;
    color: #ff8a00;
    font-size: 20px;
    font-weight: bolder;
    text-decoration: none;
    letter-spacing: 2px; }
  .header .icons {
    margin-left: 2rem; }
    .header .icons #user-btn, .header .icons #search-btn {
      color: #ba0202;
      font-size: 24px;
      margin-left: 12px;
      cursor: pointer;
      transition: .3s; }
      .header .icons #user-btn:hover, .header .icons #search-btn:hover {
        color: #F1DEDE; }
    .header .icons #user-btn {
      position: relative; }
      .header .icons #user-btn .user-menu {
        position: absolute;
        top: 30px;
        right: 12px;
        min-width: 97px;
        background: #ba0202;
        text-align: center;
        display: block;
        border-radius: 10px 0 10px 10px;
        opacity: 90%;
        clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
        transition: .3s;
        z-index: 100; }
        .header .icons #user-btn .user-menu.active {
          clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%); }
        .header .icons #user-btn .user-menu li {
          border-bottom: 1px solid rgba(255, 255, 255, 0.427); }
          .header .icons #user-btn .user-menu li:last-child {
            border-bottom: none; }
          .header .icons #user-btn .user-menu li:hover {
            background: rgba(0, 0, 0, 0.3); }
          .header .icons #user-btn .user-menu li a {
            color: #fff;
            text-decoration: none;
            line-height: 30px;
            font-size: 16px;
            display: flex;
            width: 100%;
            justify-content: center; }
  .header .search-form {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background: #000;
    padding: 10px 12%;
    opacity: 90%;
    border-top: 2px solid #1b1717;
    clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
    transition: .3s; }
    .header .search-form.active {
      clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%); }
    .header .search-form form {
      display: flex; }
      .header .search-form form input {
        width: 100%;
        outline: medium;
        margin-right: 10px; }
      .header .search-form form button {
        border: none;
        background: none;
        cursor: pointer;
        align-items: center; }
        .header .search-form form button i {
          font-size: 20px;
          color: #ba0202; }
        .header .search-form form button:hover i {
          color: #fff; }

